import React, { Component } from 'react';
import { PropTypes } from 'prop-types';

import {BootstrapTable, TableHeaderColumn} from 'react-bootstrap-table';


function copyButton(cell, row) {
  //console.log('cell' + cell);
  //var copyEvent = new ClipboardEvent('copy', {dataType: 'text/plain', data: 'cell'});

    //console.log('row' + row);
  return (
    <input type='button' class='btn-sm btn btn-outline-info' value='copy' active={ cell } onClick={() => {copyToClipboard(cell)} } />
  );
}

export default class SearchResult extends Component {

  render() {
    const { data } = this.props;
    const options = {
      searchDelayTime: 1000,
      clearSearch: true
    };
    return (
      <BootstrapTable exportCSV options={ options }
      search={ true } multiColumnSearch={ true } searchPlaceholder='Result filter'
      pagination
        data={data} striped="true" hover="true">
        <TableHeaderColumn row='0' rowSpan='2' isKey="true" dataField='uri' width='670'>URI</TableHeaderColumn>
        <TableHeaderColumn row='0' colSpan='4'> Translation</TableHeaderColumn>
        <TableHeaderColumn row='1' dataField='cn'>cn</TableHeaderColumn>
        <TableHeaderColumn row='1' dataFormat={ copyButton } width='70' dataField='cn'>Op</TableHeaderColumn>
        <TableHeaderColumn row='1' dataField='en'>en</TableHeaderColumn>
        <TableHeaderColumn row='1' dataFormat={ copyButton } width='70'dataField='en'>Op</TableHeaderColumn>
        <TableHeaderColumn dataField='key' hidden>Keys</TableHeaderColumn>
      </BootstrapTable>)
  }

}

SearchResult.propTypes = {
  data: PropTypes.arrayOf(PropTypes.object),
}

function copyToClipboard(text) {
    if (window.clipboardData && window.clipboardData.setData) {
        // IE specific code path to prevent textarea being shown while dialog is visible.
        return window.clipboardData.setData("Text", text);

    } else if (document.queryCommandSupported && document.queryCommandSupported("copy")) {
        var textarea = document.createElement("textarea");
        textarea.textContent = text;
        textarea.style.position = "fixed";  // Prevent scrolling to bottom of page in MS Edge.
        document.body.appendChild(textarea);
        textarea.select();
        try {
            return document.execCommand("copy");  // Security exception may be thrown by some browsers.
        } catch (ex) {
            console.warn("Copy to clipboard failed.", ex);
            return false;
        } finally {
            document.body.removeChild(textarea);
        }
    }
}



