export const SUGGESTION = 'SUGGESTION';
export const SEARCH_KEY = 'SEARCH_KEY';

export function suggestion(text) {
  return {
    type: 'SUGGESTION',
    text
  }
}

export function searchKey(text) {
  return {
    type: 'SEARCH_KEY',
    text
  }
}



