import React, { Component } from 'react';
import { PropTypes } from 'prop-types';
import { connect } from 'react-redux';

import logo from './ebaocloud-logo.png';
import styles from './App.css';

import {
  //suggestion,
  searchKey,
} from './actions';

import Search from './components/Search';
import SearchResult from './components/SearchResult';

class App extends Component {

  render() {
    const { dispatch, searchResult } = this.props;
    return (
      <div className={styles.app}>
        <header className={styles.appHeader} >
          <img src={logo} className={styles.appLogo} alt="logo" />
          <h1 className={styles.appTitle}>Welcome to eBaoCloud Dictionary</h1>
        </header>
        <Search
          onSearchKey={text =>
            dispatch(searchKey(text))
          }/>
        <p />
        <section>
          <SearchResult data={searchResult} />
        </section>
      </div>
    );
  }
}

App.propTypes = {
  searchResult: PropTypes.arrayOf(PropTypes.object),
};

function select(state) {
  return {
    searchResult: state.searchResult
  };
}

export default connect(select)(App);



