import React from 'react';
//import ReactDOM from 'react-dom';
import { PropTypes } from 'prop-types';
import autoBind from 'react-autobind';

import SearchBar from './search-bar';
import styles from './styles/search.css';
// import words from './words.json';

class Search extends React.Component {

  constructor(props) {
    super(props);

    this.state = {
      suggestions: [],
      words: []
    };

    autoBind(this);
  }

  componentDidMount() {

    fetch('http://localhost:30003/api/keywords').then(function(response) {
      if (response.status >= 400) {
        throw new Error("Bad response from server");
      }
      return response.json();
    }).then(items => this.setState({words: items}))

  }

  handleClear() {
    this.setState({suggestions: []});
  }

  handleChange(input) {
    this.setState({
      suggestions: this.state.words.filter(word => {
        return word.toLowerCase().indexOf(input) > -1
      })
    });

    //console.log(this.state.suggestions);
  }

  handleSelection(value) {
    if (value) {
      console.info(`Selected "${value}"`);
      this.handleSearch(value);
    }
  }

  handleSearch(value) {
    if (value) {
        //this.props.searchKey  = value;
        fetch('http://localhost:30003/api/search/' + encodeURIComponent(value))
        .then(function(response) {
          if (response.status >= 400) {
            throw new Error("Bad response from server");
          }
          return response.json();
        })
        .then(
          //translation => this.setState({translation: translation})
          //translation => {this.props.translation = translation}
          translation => this.props.onSearchKey(translation)
        )
        //this.props.onSearchKey(this.props.translation);
    }

  }

  suggestionRenderer(suggestion, searchTerm) {
    //console.info(`suggestion: "${suggestion}"  searchTerm: "${searchTerm}"`);
    var renderedSuggestion = [];
    var pStart = 0;
    var p = 0;
    while (p !== -1) {
      p = suggestion.toLowerCase().indexOf(searchTerm, pStart);
      if (p === -1) { //没有其他匹配
        renderedSuggestion.push(suggestion.substr(pStart, suggestion.length - pStart));
      } else {
        renderedSuggestion.push(suggestion.substr(pStart, p - pStart));
        renderedSuggestion.push(suggestion.substr(p - pStart, searchTerm.length));
        pStart = p + searchTerm.length;
      }
    }
    //console.log(renderedSuggestion);
    return (<span>
      {
        renderedSuggestion.map((x) => {
          if (x.toLowerCase() === searchTerm) {
            return (<strong styles='background: red'>{x}</strong>);
          } else {
            return x;
          }
        })
      }
    </span>);
  }

  render() {

    return (
      <div>
        <SearchBar
          autoFocus="autoFocus"
          renderClearButton="renderClearButton"
          renderSearchButton="renderSearchButton"
          placeholder="Searching word here"
          onChange={this.handleChange}
          onClear={this.handleClear}
          onSelection={this.handleSelection}
          onSearch={this.handleSearch}
          suggestions={this.state.suggestions}
          suggestionRenderer={this.suggestionRenderer}
          styles={styles}
          />
       </div>
     );}
}

Search.propTypes = {
  onSearchKey: PropTypes.func.isRequired,
};

export default Search;



